body {
  margin: 0;
  font-family: 'Caprasimo', cursive;
  font-size: 2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body .counterhead {
  width: 100%;
  max-heightheight: 30%;
  margin: 2% auto auto auto;
  text-align: center;
}

body .counterimg {
  width: 100%;
  max-heightheight: 40%;
  margin: 2% auto auto auto;
}

body .counterimg img {
  width: 100%;
}



@media (min-width:800px) {
  body .counterhead {
    width: 20%;
  }

  body .counterimg {
    width: 20%;
  }
}